import React from 'react'
import "../css/ticket.css"

export default function OneTicketUserHaveAddress(props) {
    const {ticketNo,by,playerAddress} = props;
    return (
        <div className="ticketBg glow-div2">
            <div className="my-auto">
                <p>Ticket No.</p>
                <h2>{ticketNo}</h2>
                <p style={{fontSize:10}}>{by}</p>
                <p>0x{playerAddress.substring(2,5)}...{playerAddress.substring(playerAddress.length-4,playerAddress.length)}</p>
            </div>
        </div>
    )
}
