import React, { useEffect, useState } from 'react'
import OneTicketUserHave from './OneTicketUserHave';
import OneTicketUserHaveAddress from './OneTicketUserHaveAddress';

import "../css/CustomButtons.css"
import "../css/MainBackground.css"
import AdminContrals from './AdminContrals';
import { useParams } from 'react-router';
import { FormattedMessage } from 'react-intl'

export default function Donation(props) {
    const { account, lotteryContract, connected, ConnectAccount, ticketPrice } = props;
    // const { networkId, networkAddress, blockExplorerUrl } = props;
    // const { networkAddress } = props;
    // console.log(" props ++++++++++++++++++++++++++++++");
    // console.log(lotteryContract);
    // console.log(" ticketPrice ++++++++++++++++++++++++++++++");
    // console.log(ticketPrice);
    // console.log(ticketPrice == 0.1*10**18);
    const { blockExplorerUrl } = props;
    const { referCode } = useParams();



    const [mainLoading, setMainLoading] = useState(true);
    const [isAdmin, setisAdmin] = useState(false);
    const [canPlayerBuyTicket, setCanPlayerBuyTicket] = useState(false);
    const [currentGameCount, setCurrentGameCount] = useState(null);
    const [ticketsLeft, setTicketsLeft] = useState(null);
    const [ticketsInDraw, setTicketsInDraw] = useState(null);
    const [playerTickets, setPlayerTickets] = useState([]);

    const [playerRefferalCode, setPlayerRefferalCode] = useState(null);
    const [onlySoldTicketNumber, setOnlySoldTicketNumber] = useState([]);

    // useEffect(() => {
    //     if (connected) {
    //         checkRefferal();
    //         loadData();
    //     }
    // }, []);

    useEffect(() => {
        if (connected) {
            checkRefferal();
            loadData();
        }
    }, [lotteryContract]);


    const checkRefferal = () => {
        if (referCode != null) {
            setPlayerRefferalCode(referCode)
        } else {
            setPlayerRefferalCode(null);
        }
    }

    const connectToAccount = () => {
        if (!connected) {
            setMainLoading(true);
            ConnectAccount()
        }
    }

    const loadData = async () => {
        setMainLoading(true);
        const getCanPlayerBuyTicket = await lotteryContract.methods.getCanPlayerBuyTicket().call();
        setCanPlayerBuyTicket(getCanPlayerBuyTicket);

        const isAdmin = await lotteryContract.methods.isAdmin().call();
        setisAdmin(isAdmin);

        const getCurrentGameCount = await lotteryContract.methods.getCurrentGameCount().call();
        setCurrentGameCount(getCurrentGameCount.toNumber());

        const getTotalTickets = await lotteryContract.methods.getTotalTickets().call();
        const getOnlySoldTicketOfGame = await lotteryContract.methods.getOnlySoldTicketOfGame(getCurrentGameCount.toNumber()).call();
        setTicketsLeft(getTotalTickets.toNumber() - getOnlySoldTicketOfGame.toNumber());

        const getTotalTicketOfGame = await lotteryContract.methods.getTotalTicketOfGame(getCurrentGameCount.toNumber()).call();
        setTicketsInDraw(getTotalTicketOfGame.toNumber());

        const getAllTicketsOfUserInGame = await lotteryContract.methods.getAllTicketsOfUserInGame(account, getCurrentGameCount.toNumber()).call();
        setPlayerTickets(getAllTicketsOfUserInGame);

        const getOnlySoldTicketNumber = await lotteryContract.methods.getOnlySoldTicketNumber(getCurrentGameCount.toNumber()).call();
        // // setOnlySoldTicketNumber(getOnlySoldTicketNumber);
        console.log("getOnlySoldTicketNumber");
        console.log(getOnlySoldTicketNumber);
        setOnlySoldTicketNumber(getOnlySoldTicketNumber);
        setMainLoading(false);

    }

    const loadDataAuto = async () => {
        const getCanPlayerBuyTicket = await lotteryContract.methods.getCanPlayerBuyTicket().call();
        setCanPlayerBuyTicket(getCanPlayerBuyTicket);

        const isAdmin = await lotteryContract.methods.isAdmin().call();
        setisAdmin(isAdmin);

        const getCurrentGameCount = await lotteryContract.methods.getCurrentGameCount().call();
        setCurrentGameCount(getCurrentGameCount.toNumber());

        const getTotalTickets = await lotteryContract.methods.getTotalTickets().call();
        const getOnlySoldTicketOfGame = await lotteryContract.methods.getOnlySoldTicketOfGame(getCurrentGameCount.toNumber()).call();
        setTicketsLeft(getTotalTickets.toNumber()*3 - getOnlySoldTicketOfGame.toNumber());

        const getTotalTicketOfGame = await lotteryContract.methods.getTotalTicketOfGame(getCurrentGameCount.toNumber()).call();
        setTicketsInDraw(getTotalTicketOfGame.toNumber());

        const getAllTicketsOfUserInGame = await lotteryContract.methods.getAllTicketsOfUserInGame(account, getCurrentGameCount.toNumber()).call();
        setPlayerTickets(getAllTicketsOfUserInGame);
    }
    setInterval(() => {
        if (lotteryContract) {

            loadDataAuto();
        }
    }, 5000);

    const buyTicket = async (item) => {
        console.log("buyTicket params: ", item)
        if (connected && lotteryContract != null) {
            setMainLoading(true);
            const getCanPlayerBuyTicket = await lotteryContract.methods.getCanPlayerBuyTicket().call();
            if (getCanPlayerBuyTicket) {
                if (playerRefferalCode != null) {
                    console.log("buyTicketwithRefferalCode");
                    buyTicketwithRefferalCode(item);
                } else {
                    console.log("buyTicketWithoutRefferal");
                    buyTicketWithoutRefferal(item)
                }
            } else {
                alert('game not started yet')
            }
        } else {
            setMainLoading(false);
            connectToAccount();
        }
    }

    const buyTicketWithoutRefferal = async (item) => {
        console.log("buyTicketWithoutRefferal params: ", item)
        await lotteryContract.methods.buyTicket(item)
            .send({ from: account, value: ticketPrice })
            .on('confirmation', (confirmationNumber, receipt) => {
                loadData();
            })
            .on('error', function (error, receipt) {
                loadData();
            });
    }

    const buyTicketwithRefferalCode = async (item) => {
        await lotteryContract.methods.buyTicketUsingReferral(playerRefferalCode, item)
            .send({ from: account, value: ticketPrice })
            .on('confirmation', (confirmationNumber, receipt) => {
                loadData();
            })
            .on('error', function (error, receipt) {
                loadData();
            });
    }

    const depositInRewardsPool = async (_money) => {
        console.log("depositInRewardsPool params: ", _money)
        await lotteryContract.methods.buyTicket(0)
            .send({ from: account, value: _money })
            .on('confirmation', (confirmationNumber, receipt) => {
                loadData();
            })
            .on('error', function (error, receipt) {
                loadData();
            });
    }
  

    return (
        <div className="main-box mx-auto mt-2 p-4 text-light text-center">
            <div className="bg-holder" style={{ backgroundImage: 'url(img/bg/heroheader-bg.png)', backgroundPosition: 'center', backgroundSize: 'contain' }}></div>

            {connected && <h2>
                            <FormattedMessage
                                id="donation_title"
                                defaultMessage="donation title"
                            /></h2>
            }
            {
                <div>  
                    <p>              </p>
                    <p>              </p>
                    <p>              </p>
                    <p>              </p>
                    <br></br>
                </div>
            }

            <button onClick={() => { depositInRewardsPool(1) }} className="btn btn-glow custom-Big-Button text-light mt-5">
                <div className="d-flex justify-content-center align-items-center">
                    {/* <img className="img-fluid" src="img/maticlogo.png" alt="" style={{ height: 100 }} /> */}
                    <div className="">
                        <h2>1</h2>
                        {/* <h6>For {ticketPrice / (10 ** 18)} Matic</h6> */}
                    </div>
                </div>
            </button>
            
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <button onClick={() => { depositInRewardsPool(5) }} className="btn btn-glow custom-Big-Button text-light mt-5">
                <div className="d-flex justify-content-center align-items-center">
                    {/* <img className="img-fluid" src="img/maticlogo.png" alt="" style={{ height: 100 }} /> */}
                    <div className="">
                        <h2>5</h2>
                        {/* <h6>For {ticketPrice / (10 ** 18)} Matic</h6> */}
                    </div>
                </div>
            </button>

            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <button onClick={() => { depositInRewardsPool(10) }} className="btn btn-glow custom-Big-Button text-light mt-5">
                <div className="d-flex justify-content-center align-items-center">
                    {/* <img className="img-fluid" src="img/maticlogo.png" alt="" style={{ height: 100 }} /> */}
                    <div className="">
                        <h2>10</h2>
                        {/* <h6>For {ticketPrice / (10 ** 18)} Matic</h6> */}
                    </div>
                </div>
            </button>

            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <button onClick={() => { depositInRewardsPool(100) }} className="btn btn-glow custom-Big-Button text-light mt-5">
                <div className="d-flex justify-content-center align-items-center">
                    {/* <img className="img-fluid" src="img/maticlogo.png" alt="" style={{ height: 100 }} /> */}
                    <div className="">
                        <h2>100</h2>
                        {/* <h6>For {ticketPrice / (10 ** 18)} Matic</h6> */}
                    </div>
                </div>
            </button>
   
        </div >
    )
}
